@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

:root {
  --Soft-orange: hsl(35, 77%, 62%);
  --Soft-red: hsl(5, 85%, 63%);
  --Off-white: hsl(36, 100%, 99%);
  --Grayish-blue: hsl(233, 8%, 79%);
  --Dark-grayish-blue: hsl(236, 13%, 42%);
  --Very-dark-blue: hsl(240, 100%, 5%);
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: "Inter", sans-serif;
  background-color: white;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 375px;
  height: 100vh;
  background-color: var(--Off-white);
}
main {
  width: 375px;
}
header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
.desktop-nav {
  display: none;
}
.mobile-nav {
  position: relative;
}
.hamburger {
  border: 0;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  z-index: 999;
}
.menu {
  border: none;
  background-color: var(--Off-white);
  z-index: 3;
}
.links {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: -240px;
  top: 0;
  width: 500px;
  height: 1800px;
  background-color: white;
  z-index: 1;
  padding: 100px 30px;
}
.links a {
  margin: 12px 0;
  text-decoration: none;
  color: black;
  font-weight: 500;
  font-size: larger;
}
.links a:hover {
  color: var(--Soft-red);
}
.one {
  width: 375px;
  margin-bottom: 40px;
}
.web3-image-container {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}
.web3-image-container .web3-mobile-img {
  width: 375px;
}
.web3-image-container .web3-destop-img {
  display: none;
}
.web3-content h1 {
  font-size: 3em;
  font-weight: 900;
  margin-bottom: 20px;
}
.web3-content p {
  font-weight: 600;
  color: rgba(12, 12, 12, 0.5);
  margin-bottom: 20px;
}
.web3-content button {
  padding: 15px 40px;
  background-color: rgba(255, 72, 0, 0.8);
  color: white;
  font-size: large;
  letter-spacing: 3px;
  border: none;
  margin-bottom: 20px;
}
.web3-content button:hover {
  background-color: black;
}
/* section two  */
.two {
  background-color: black;
  padding: 20px;
}
.two h2 {
  color: var(--Soft-orange);
}
.two .sub-heading {
  margin: 40px 0;
  border-bottom: 1px solid var(--Dark-grayish-blue);
  padding-bottom: 30px;
  border-spacing: 20px;
}
.two .sub-heading:last-child {
     border: none;
     padding-bottom: 0;
     margin-bottom: 20px;
}
.two .sub-heading a {
  text-decoration: none;
  color: var(--Off-white);
  font-size: large;
  margin-bottom: 10px;
  letter-spacing: 1px;
  word-spacing: 2px;
}
.two .sub-heading a:hover {
  color: var(--Soft-orange);
}
.two .sub-heading p {
  color: var(--Dark-grayish-blue);
  margin-top: 10px;
}
/* section 3 */
.three {
  margin: 40px 0;
}
.hottest {
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin-bottom: 20px;
}
.hottest .hottest-img {
  height: auto;
}
.hottest .hottest-img img {
  height: 100px;
}
.hottest .hottest-content h2 {
  color: var(--Grayish-blue);
  margin-bottom: 5px ;
}
.hottest .hottest-content .sub-heading {
  font-weight: 900;
  margin-bottom: 7px;
}
.hottest .hottest-content .sub-heading a {
  text-decoration: none;
  color: black;
}
.hottest .hottest-content .sub-heading a:hover {
  color: var(--Soft-red);
}





/* medias */ 
@media only screen and (min-width: 750px) {
  .desktop-nav {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
  .desktop-nav a {
    text-decoration: none;
    color: black;
  }
  .desktop-nav a:hover {
    color: var(--Soft-red);
  }
  .mobile-nav {
    display: none;
  }
  #root {
    width: 600px;
    justify-content: center;
    display: flex;
    align-items: center;
  }
  main {
    width: 700px;
    display: grid;
    grid-template-rows: repeat(3, 200px);
    grid-template-areas: 
        "sect-one sect-one sect-two"
        "sect-one sect-one sect-two"
        "sect-three sect-three sect-three";
    column-gap: 40px
  }
  .one {
    width: 400px;
    grid-area: sect-one;
    display: grid;
    grid-template-columns: 300px 200px;
    grid-template-rows: 250px 200px;
    grid-template-areas: 
        "img img"
        "content content";
  }
  .web3-image-container {
    grid-area: img;
    margin-bottom: 0;
  }
  
  .web3-content {
    grid-area: content;
    display: flex;
    gap: 10px;
    margin-top: 20px;
  }
  .web3-content .h1-container {
     width: 100%;
  }
  .web3-content .about {
    width: 100%;
    text-align: justify;
  }
  .web3-content h1 {
    font-size: 2.5em;
  }
  .web3-content p {
    font-size: smaller;
  }
  .web3-content button {
    padding: 10px;
    font-size: small;
  }
  
  .two {
    grid-area: sect-two;
    padding: 12px;
  }
  .two .sub-heading {
    margin: 10px 0;
    border-bottom: 1px solid var(--Dark-grayish-blue);
    padding-bottom: 10px;
    border-spacing: 20px;
  }
  .two .sub-heading a {
    text-decoration: none;
    color: var(--Off-white);
    font-size: x-small;
  }                
  .two .sub-heading p {
    color: var(--Dark-grayish-blue);
    margin-top: 10px;
    font-size: x-small;
  }
  .two .sub-heading:last-child {
    border: none;
    padding-bottom: 0;
    margin-bottom: 0;
}
  .three {
    grid-area: sect-three;
    display: flex;
  }
  
  .hottest {
    display: flex;
    flex-direction: row;
    gap: 10px;
    margin-bottom: 20px;
  }
  .hottest .hottest-img {
    height: auto;
  }
  .hottest .hottest-img img {
    height: 80px;
  }
  .hottest .hottest-content h2 {
    color: var(--Grayish-blue);
    margin-bottom: 10px ;
    font-size: large;
  }
  .hottest .hottest-content .sub-heading {
    font-weight: 900;
    margin-bottom: 10px;
    font-size: x-small;
  }
  .hottest .hottest-content .about {
    font-size: x-small;
  }
  .web3-image-container .web3-mobile-img {
    display: none;
  }
  .web3-image-container .web3-destop-img {
    display: block;
    width: 500px;
    height: 250px;
  }

}


@media only screen and (min-width: 1200px) {
  .desktop-nav {
    display: flex;

  }
  .mobile-nav {
    display: none;
  }
  #root {
    width: 1000px;
    justify-content: center;
  }
  main {
    width: 1000px;
    display: grid;
    grid-template-rows: repeat(3, 200px);
    grid-template-columns: 650px;
    grid-template-areas: 
        "sect-one sect-one sect-two"
        "sect-one sect-one sect-two"
        "sect-three sect-three sect-three";
    column-gap: 40px;
    
  }
  .one {
    width: 400px;
    grid-area: sect-one;
    display: grid;
    grid-template-columns: 500px 200px;
    grid-template-rows: 250px 200px;
    grid-template-areas: 
        "img img"
        "content content";
  }
  .web3-image-container .web3-destop-img {
    display: block;
    width: 700px;
    height: 250px;
  }
  .web3-content h1 {
    font-size: 2.4em;
    width: 250px;
  }
  .web3-content button {
    padding: 10px;
    font-size: small;
  }
  .web3-content .about {
     width: 1000px;
  }
  .web3-content .about p {
    font-size: medium;
  }

  .two {
    grid-area: sect-two;
    padding: 12px;
  }
  .two .sub-heading {
    margin: 20px 0;
    border-bottom: 1px solid var(--Dark-grayish-blue);
    padding-bottom: 30px;
    border-spacing: 20px;
  }
  .two .sub-heading a {
    text-decoration: none;
    color: var(--Off-white);
    font-size: small;
  }                
  .two .sub-heading p {
    color: var(--Dark-grayish-blue);
    margin-top: 10px;
    font-size: small;
  }
  .three {
    justify-content: space-between;
  }
  .hottest {
    width: 250px;
  }
  
}



